import {Grid, List, Paper, Typography} from "@mui/material";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";
import AccountBalanceIcon from "@mui/icons-material/AccountBalance";
import LocalFireDepartmentIcon from "@mui/icons-material/LocalFireDepartment";
import CachedIcon from "@mui/icons-material/Cached";
import {useEffect, useState} from "react";
import {useSelector} from "react-redux";
import {DivvyCryptoFormat} from "../utils";
import StatDetail from "./StatDetail";

export default function ProjectBurnSupply(props) {

    const {project} = props;
    const {projectsMap} = useSelector(state => state.divvy);
    const {stats} = projectsMap[project.contract];
    const [percent, setPercent] = useState(0);
    const [angle, setAngle] = useState(0);

    useEffect(() => {
        if (stats && stats.burnt && stats.supply) {
            setPercent(stats.burnt / stats.supply);
            setAngle(360 * (stats.burnt / stats.supply) * (Math.PI / 180))
        }
    }, [stats]);

    return <Paper elevation={0}>
        <Typography variant="h6">{project.symbol} Supply</Typography>
        <Grid container>
            <Grid item xs={12} md={6}>
                <FlexibleWidthXYPlot
                    xDomain={[-5, 5]}
                    yDomain={[-5, 5]}
                    height={230}
                    margin={{left: 0, right: 0, top: 0, bottom: 0}}
                >
                    <ArcSeries
                        radiusDomain={[0, 10]}
                        center={{x: 0, y: 0}}
                        data={[
                            {
                                angle0: 0,
                                angle: Math.PI * 2,
                                opacity: 1,
                                radius: 6.75,
                                radius0: 6,
                                color: '#eee',
                            },
                            {
                                angle0: 0,
                                angle: angle,
                                opacity: 1,
                                radius: 8.5,
                                radius0: 6,
                                color: project.primary,
                            },
                        ]}
                        colorType={'literal'}
                    />
                </FlexibleWidthXYPlot>
                <Typography
                    style={{
                        height: 0,
                        width: '100%',
                        textAlign: 'center',
                        top: -150,
                        position: 'relative',
                        fontSize: 30,
                        fontWeight: '700',
                        color: project.primary
                    }}
                >
                    {(percent * 100).toFixed(2)}
                    <span
                        style={{
                            fontSize: 12,
                            fontWeight: '400',
                            marginTop: -10,
                            display: 'block',
                        }}
                    >% BURNED</span>
                </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
                <List disablePadding>
                    <StatDetail
                        avatar={<AccountBalanceIcon sx={{color: project.primary}}/>}
                        primary={DivvyCryptoFormat(stats.supply) + ` ${project.symbol}`}
                        secondary="Total Supply"
                    />
                    <StatDetail
                        avatar={<LocalFireDepartmentIcon sx={{color: project.primary}}/>}
                        primary={DivvyCryptoFormat(stats.burnt) + ` ${project.symbol}`}
                        secondary="Burnt Supply"
                    />
                    <StatDetail
                        avatar={<CachedIcon sx={{color: project.primary}}/>}
                        primary={DivvyCryptoFormat(stats.supply - stats.burnt) + ` ${project.symbol}`}
                        secondary="Circulating Supply"
                    />
                </List>
            </Grid>
        </Grid>
    </Paper>;
}