import {Avatar, Grid, List, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import {BNBIcon} from "../utils";
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import StatDetail from "./StatDetail";

export default function PersonalStats(props) {

    const {project} = props;
    const {bnbPrice, wallet, projectsMap} = useSelector(state => state.divvy);
    const {stats} = projectsMap[project.contract];
    const balance = wallet.balances[project.contract];

    return <Paper elevation={0}>
        <Grid container spacing={2}>
            <Grid item>
                <Avatar><img src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0x820671E3647d8aaa22Feab05aaA14AB1665e06e1/logo.png" width={40} /></Avatar>
            </Grid>
            <Grid item>
                <Typography variant="h6">My Investment <Typography variant="caption">sponsored by FOOK</Typography></Typography>
            </Grid>
        </Grid>
        <List disablePadding>

            <StatDetail
                avatar={<AccountBalanceWalletIcon sx={{color: project.primary}}/>}
                primary={`${DivvyCryptoFormat(balance)} ${project.symbol}`}
                secondary="Balance"
            />
            <StatDetail
                avatar={<BNBIcon color={project.primary}/>}
                primary={`${DivvyCryptoFormat(balance * stats.price)} BNB`}
                secondary="Value BNB"
            />
            <StatDetail
                avatar={<AttachMoneyIcon sx={{color: project.primary}}/>}
                primary={`${DivvyUSDFormat(balance * stats.price * bnbPrice)}`}
                secondary="Value USD"
            />
        </List>
    </Paper>
}