import {Link, List, Paper, Typography} from "@mui/material";
import StatDetail from "./StatDetail";

import LanguageIcon from '@mui/icons-material/Language';
import TwitterIcon from "@mui/icons-material/Twitter";
import TelegramIcon from "@mui/icons-material/Telegram";
import ShowChartIcon from "@mui/icons-material/ShowChart";
import FindInPageIcon from '@mui/icons-material/FindInPage';
import SwapHorizIcon from '@mui/icons-material/SwapHoriz';


export default function ProjectLinks(props) {

    const {project, infoJSON} = props;

    return <Paper elevation={0}>
        <Typography variant="h6">{project.symbol} Links</Typography>
        <List disablePadding>

            <StatDetail
                avatar={<ShowChartIcon sx={{color: project.primary}}/>}
                primary={<Link href={`https://charts.fegex.com/charts/${project.contract}`} underline="none" style={{color: 'inherit'}} target="_blank">FEGcharts</Link>}
            />

            <StatDetail
                avatar={<SwapHorizIcon sx={{color: project.primary}}/>}
                primary={<Link href={`https://fegex.com/trade/bsc/${project.contract}`} underline="none" style={{color: 'inherit'}} target="_blank">FEGex</Link>}
            />
            {
                infoJSON.website && <StatDetail
                    avatar={<LanguageIcon sx={{color: project.primary}}/>}
                    primary={<Link href={infoJSON.website} underline="none" style={{color: 'inherit'}} target="_blank">Website</Link>}
                />
            }

            {
                infoJSON.social && infoJSON.social.twitter && <StatDetail
                    avatar={<TwitterIcon sx={{color: project.primary}}/>}
                    primary={<Link href={infoJSON.social.twitter} underline="none" style={{color: 'inherit'}} target="_blank">Twitter</Link>}
                />
            }
            {
                infoJSON.social && infoJSON.social.telegram && <StatDetail
                    avatar={<TelegramIcon sx={{color: project.primary}}/>}
                    primary={<Link href={infoJSON.social.telegram} underline="none" style={{color: 'inherit'}} target="_blank">Telegram</Link>}
                />
            }
            {
                infoJSON.explorer && <StatDetail
                    avatar={<FindInPageIcon sx={{color: project.primary}}/>}
                    primary={<Link href={`https://bscscan.com/token/${project.contract}`} underline="none" style={{color: 'inherit'}} target="_blank">BSCscan</Link>}
                />
            }
        </List>
    </Paper>
}