import {ListItem, ListItemAvatar, ListItemText} from "@mui/material";

export default function StatDetail(props) {

    const {avatar, primary, secondary} = props;

    return <ListItem disableGutters>
        <ListItemAvatar style={{textAlign: 'center'}}>
            {avatar}
        </ListItemAvatar>
        <ListItemText
        primary={primary}
        secondary={secondary}
        primaryTypographyProps={{style:{fontWeight: 700}}}
        />
    </ListItem>
}