import {Avatar, Button, Grid, List, ListItem, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useDispatch, useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import LockIcon from '@mui/icons-material/Lock';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import {useEffect, useState} from "react";
import Web3 from "web3";
import faucet_abi from '../abis/faucet_abi.json';
import {updateFaucetRewards} from "../redux/slices/divvySlice";
import StatDetail from "./StatDetail";

export default function ProjectFaucet(props) {

    const {project} = props;
    const {bnbPrice, projectsMap, wallet} = useSelector(state => state.divvy);
    const {faucet, stats} = projectsMap[project.contract];
    const [contract, setContract] = useState(null);
    const dispatch = useDispatch();

    useEffect(() => {
        if (wallet.address && window.provider) {
            const web3 = new Web3(window.provider);
            setContract(new web3.eth.Contract(faucet_abi, project.faucet));
        }
    }, [wallet.address, window.provider]);

    useEffect(() => {
        const interval = setInterval(() => {
            const web3 = new Web3('https://bsc-dataseed.binance.org/');
            const c = new web3.eth.Contract(faucet_abi, project.faucet);
            c.methods.availableReflections().call((err, resp) => {
                if (err) {
                    console.log(err);
                    return;
                }
                dispatch(updateFaucetRewards({
                    contract: project.contract,
                    faucet_rewards: resp / Math.pow(10, 9) || 0
                }))
            })
        }, 10000);

        return () => clearInterval(interval);
    }, []);

    const claimFaucet = () => {
        if (contract) contract.methods.GetFaucetRewards().send({from: wallet.address});
    }

    if (!faucet) {
        return null
    }

    return <Paper elevation={0}>
        <Grid container spacing={2}>
            <Grid item>
                <Avatar><img src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0xD0685D66a23F86594C5f74B69518ad32B6C2F411/logo.png" width={40} /></Avatar>
            </Grid>
            <Grid item>
                <Typography variant="h6">Lava Flow <Typography variant="caption">sponsored by LAVA</Typography></Typography>
            </Grid>
        </Grid>
        <List disablePadding>
            <StatDetail
                avatar={<LockIcon sx={{color: project.primary}}/>}
                primary={`${DivvyCryptoFormat(faucet.locked.toFixed(0))} ${project.symbol}`}
                secondary="Locked"
            />
            <StatDetail
                avatar={<LocalAtmIcon sx={{color: project.primary}}/>}
                primary={`${DivvyCryptoFormat(faucet.reflections)} ${project.symbol}`}
                secondary="Available"
            />
            <StatDetail
                avatar={<AttachMoneyIcon sx={{color: project.primary}}/>}
                primary={`${DivvyUSDFormat(faucet.reflections * stats.price * bnbPrice)}`}
                secondary="Value"
            />
            <ListItem>
                {
                    wallet.address ? <Button
                            variant="contained"
                            onClick={claimFaucet}
                            disabled={faucet.reflections <= 0}
                        >Claim {project.symbol}</Button>
                        :
                        <></>
                }
            </ListItem>
        </List>
    </Paper>
}